import axios from 'axios';
import moment from 'moment';
import { Container, Accordion } from "react-bootstrap";
import { useContext, useEffect, useState } from 'react';
import Select from "react-select";
import { useHistory, useParams } from 'react-router'
import { Context } from '../context/Context'
import Swal from 'sweetalert2';

// import "./privileages.css"
const UserPrivileage = ()=>{
    const { user, dispatch } = useContext(Context)
  const { id } = useParams()
  const [evangelist, setEvangelist] = useState(null)
  const [error, setError] = useState(null)
  const [isLoading, setIsLoading] = useState(true)

  useEffect(()=>{
    const config = {
        headers: { Authorization: `Bearer ${user.jwt}` },
      }
  
      axios
        .get(`https://evangelism.thecophq.org/api/users/${id}`, config)
        .then((result) => {
          setEvangelist(result.data)
        //   console.log(result.data);
          setError(null)
          setIsLoading(false)
        })
        .catch((err) => {
          // console.log("an error occured");
          setError('An error has been encountered, please try again later')
          setIsLoading(false)
        });
  },[id])
    return(
        <div className="container py-2" style={{marginTop:"8rem", backgroundColor:"#e2e8f0"}}>
            {error && (
        <div className="alert alert-danger" role="alert">
          <p>{error}</p>
        </div>
      )}
      {isLoading && (
        <div className="text-center">
          <div
            className="spinner-border text-info "
            role="status"
            style={{ marginTop: '60px' }}
          >
            <span className="visually-hidden">Loading...</span>
          </div>
        </div>
      )}
           {evangelist && <div className="row mx-auto mt-5">
           <div className="col-3 mb-3">
           <div className="card">
                <div className="card-body">
                  <div className="d-flex flex-column align-items-center text-center">
                    <img src="https://bootdey.com/img/Content/avatar/avatar7.png" alt="Admin" className="rounded-circle" width="150"/>
                    <div className="mt-3">
                      <h4>{evangelist.first_name}{' '}{evangelist.last_name}</h4>
                      <p className="text-secondary mb-1">Role of the user</p>
                      <p className="text-muted font-size-sm">Evangelist</p>
                      <button className="btn btn-outline-primary">Grant Evangelist Privilege</button>
                    </div>
                  </div>
                </div>
              </div>
           </div>
           <div className="col-7 mb-3">
           <div className="card mb-3">
                <div className="card-body">
                  <div className="row">
                    <div className="col-sm-3">
                      <h6 className="mb-0">First Name</h6>
                    </div>
                    <div className="col-sm-9 text-secondary">
                      {evangelist.first_name}
                    </div>
                  </div>
                  <hr/>
                  <div className="row">
                    <div className="col-sm-3">
                      <h6 className="mb-0">Last Name</h6>
                    </div>
                    <div className="col-sm-9 text-secondary">
                      {evangelist.last_name}
                    </div>
                  </div>
                  <hr/>
                  <div className="row">
                    <div className="col-sm-3">
                      <h6 className="mb-0">Email</h6>
                    </div>
                    <div className="col-sm-9 text-secondary">
                      {evangelist.email}
                    </div>
                  </div>
                  <hr/>
                  <div className="row">
                    <div className="col-sm-3">
                      <h6 className="mb-0">Status</h6>
                    </div>
                    <div className="col-sm-9 text-secondary">
                      {evangelist.role.name}
                    </div>
                  </div>
                  <hr/>
                  <div className="row">
                    <div className="col-sm-3">
                      <h6 className="mb-0">Address</h6>
                    </div>
                    <div className="col-sm-9 text-secondary">
                      Bay Area, San Francisco, CA
                    </div>
                  </div>
                  <hr/>
                  
                    <div className="col-sm-12 text-center" >
                      <button className="btn btn-outline-danger" style={{width:"90%"}}>Remove this user</button>
                    </div>
                  
                </div>
              </div>
           </div>
           </div>}
        </div>
    );
}

export default UserPrivileage;